<template>
	<div class="topSearchBg" v-bind:style="{ background: 'url('+bg+')' }">
		<el-autocomplete class="searchArea" v-model="state4" :fetch-suggestions="querySearchAsync"
			placeholder=""  @select="handleSelect">
		</el-autocomplete>
    <router-link to="/ranking" target="_blank" class="rankq" v-bind:style="{ background: 'url('+ranking+') no-repeat left center' }" title="cjoyful"></router-link>
		<el-button type="primary" class="searchBtn" icon="search"></el-button>
    <div class="bgLogo" v-bind:style="{ background: 'url('+logo+')' }"></div>  
	</div>
</template>

