      return {
      	bg: require('../assets/images/search-bg.png'),
      	logo: require('../assets/images/logo.png'),
      	ranking: require('../assets/images/rankingList.png'),
        restaurants: [],
        state4: '',
        timeout:  null
      };
    },
    methods: {
      loadAll() {
        return [
          { "value": "", "address": "144" },
          { "value": "Hot honey ", "address": "661" },
          { "value": "", "address": "9886113" },
          { "value": "()", "address": "438" },
          { "value": "", "address": "96811818-101" },
          { "value": "", "address": "633" },
          { "value": "", "address": "1685" },
          { "value": "", "address": "1435" },
          { "value": "", "address": "144481B-107" },
          { "value": "", "address": "817" },
          { "value": "/", "address": "1611" },
          { "value": "", "address": "238355" },
          { "value": "Monica", "address": "24091F2383621F" },
          { "value": "soho", "address": "9689" },
          { "value": "NONO JUICE  ", "address": "119" },
          { "value": "CoCo(", "address": "" },
          { "value": "", "address": "5671R117" },
          { "value": "Merci Paul cafe", "address": "286819" },
          { "value": "", "address": "88G05-F01-1-306" },
          { "value": "", "address": "" },
          { "value": "", "address": "() 267" },
          { "value": "", "address": "" },
          { "value": "", "address": "" },
