          { "value": "()", "address": "882" },
          { "value": "", "address": "389" },
          { "value": "", "address": "1699A13" }
        ];
      },
      querySearchAsync(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          cb(results);
        }, 3000 * Math.random());
      },
      createStateFilter(queryString) {
        return (state) => {
          return (state.value.indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
    }
  };
</script>

<style scoped>
	.topSearchBg {
		height: 180px;
		position: relative;
	}
	.searchArea {
		position: absolute;
		right: 90px;
		bottom: 5px;
	}
	.searchBtn {
		position: absolute;
		right: 10px;
		bottom: 5px;
	}
	.bgLogo {
		position: absolute;
		width: 220px;
		height: 84px;
		left: 40px;
		bottom: 25px;
	}
	.rankq {
		width: 20px;
    	height: 38px;
    	position: absolute;
    	bottom: 5px;
    	right: 100px;
    	z-index: 2;
	}
	@media screen and (min-width: 600px) {
		.searchArea {
			width: 300px;
		}
	}
</style>


